.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("../../assets/image3.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;

  display: flex;
  justify-content: center;
  // align-items: center;

  .main {
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;

    .title {
      color: #ececec;
      font-style: italic;
      font-size: 56px;
      line-height: 1.2;
    }

    .icons {
      .icon {
        color: #ececec;
        padding: 20px 20px;
        text-decoration: none;
      }

      .icon:hover {
        color: #444;
      }
    }
  }
}
